.form {
  input {
    display: block;

    width: 100%;
    margin-bottom: 20px;
    padding: 11px 30px;

    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    letter-spacing: normal;
    color: $form-input_text;

    background: $form-input_bg;
    border: 1px solid $form-input_border;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    transition: background 0.3s ease;

    &:hover,
    &:focus {
      padding: 9px 28px;
      border: 3px solid $accent;
    }

    @media (max-width: $tablet-width) {
      padding: 11px 20px;

      &:hover,
      &:focus {
        padding: 9px 18px;
      }
    }

    @media (max-width: $mobile-width) {
      padding: 11px 26px;

      &:hover,
      &:focus {
        padding: 9px 24px;
      }
    }
  }

  .form__error,
  .form__error:hover,
  .form__error:focus {
    border: 3px solid $form-input_border-error;
    padding: 9px 28px;

    @media (max-width: $tablet-width) {
      padding: 9px 18px;
    }

    @media (max-width: $mobile-width) {
      padding: 9px 24px;
    }
  }
}
